import * as React from "react";
import {
  Card,
  CardContent,
  CardHeader,
  Typography,
  Avatar
} from "@mui/material";
import FormatQuoteIcon from "@mui/icons-material/FormatQuote";

const ReviewCard = ({ review }) => {
  const { author, title, content, avatar } = review;

  return (
    <Card>
      <FormatQuoteIcon
        sx={{ mr: "auto", pl: "25%", pt: 2, display: "block" }}
      />
      <CardContent>
        <Typography
          variant="body2"
          color="text.secondary"
          sx={{ textAlign: "justify" }}
        >
          {content}
        </Typography>
      </CardContent>
      <CardHeader
        title={author}
        subheader={title}
        avatar={avatar || <Avatar />}
      />
    </Card>
  );
};

export default ReviewCard;
